.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 55%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out;
}

.SideDrawer .logo-wrap{
text-align: center;
margin-bottom: 3rem;
}

@media (min-width: 990px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    transform: translateX(0);
}

.Close {
    transform: translateX(-100%);
}

